:global(:root) {
  /* dark trading dashboard palette (local overrides) */
  --bg: #07101a;
  --card-bg: #0f1720;
  --text: #e6eef8;
  --muted: #9aa6b2;
  --primary: #7c5cff;
  --accent: #22c1c3; /* teal-ish */
  --danger: #ff6b6b;
  --muted-border: rgba(255,255,255,0.04);
}

/* New layout wrapper for list + detail inside the right column */
.listAndDetailWrap {
  display: grid;
  grid-template-columns: 360px 1fr; /* fixed left list, flexible right detail */
  gap: 16px;
  align-items: start;
  width: 100%;
}

.leftPane {
  background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));
  border-radius: 10px;
  padding: 10px;
  min-height: 420px;
  box-shadow: 0 6px 24px rgba(2,8,23,0.5);
}

.rightPane {
  background: linear-gradient(180deg, rgba(10,14,18,0.6), rgba(6,10,14,0.4));
  border-radius: 10px;
  padding: 12px;
  min-height: 420px;
  box-shadow: 0 6px 24px rgba(2,8,23,0.35);
}

/* tweak existing left/right panels to be darker and compact */
.left {
  background: linear-gradient(180deg, rgba(6,10,14,0.95), rgba(6,10,14,0.9));
  padding: 12px;
  border-radius: 12px;
}

.right {
  background: transparent;
  padding: 6px 0;
}

/* make table area darker and more subtle */
.table {
  background: linear-gradient(180deg, rgba(255,255,255,0.01), rgba(255,255,255,0.005));
  border-radius: 8px;
  color: var(--text);
}

.table th, .table td { color: var(--muted); }

/* compact list area inside leftPane */
.positionsList {
  max-height: calc(68vh - 120px);
  overflow: auto;
  padding-right: 6px;
}

/* subtle scrollbar style for modern look (webkit browsers) */
.positionsList::-webkit-scrollbar {
  width: 8px;
}
.positionsList::-webkit-scrollbar-thumb {
  background: rgba(255,255,255,0.04);
  border-radius: 6px;
}

/* small responsive adjustment */
@media (max-width: 980px) {
  .listAndDetailWrap {
    grid-template-columns: 1fr; /* stack vertically on small widths */
  }
  .leftPane { order: 1 }
  .rightPane { order: 2 }
}

/* Small and extra-small screens tweaks */
@media (max-width: 768px) {
  .listAndDetailWrap {
    gap: var(--space-3, 12px);
  }

  .grid {
    grid-template-columns: 1fr; /* stack columns for medium/small screens */
  }

  .left, .right, .leftPane, .rightPane {
    padding: var(--space-3, 12px);
    border-radius: calc(var(--card-radius, 10px));
  }

  /* Make the table area scroll horizontally instead of forcing wide min-width */
  .table {
    min-width: 0; /* allow table to shrink inside a scroll container */
  }
  .tableWrap {
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
  }

  /* Make controls and buttons more touch-friendly */
  .controls, .filterPills, .actions {
    gap: var(--space-2, 8px);
  }

  .title { font-size: var(--fs-md, 16px); }
  .table th, .table td { font-size: var(--fs-sm, 14px); }
}

/* Very small screens (phones) - convert the list rows into stacked cards for better readability */
@media (max-width: 420px) {
  .positionsList {
    max-height: 50vh;
  }

  /* Hide table header and render rows as card-like blocks via CSS only fallback when possible */
  .table thead { display: none; }
  .table, .table tbody, .table tr, .table td { display: block; width: 100%; }

  .table tr { margin: 0 0 var(--space-3,12px); border-radius: var(--card-radius, 8px); background: var(--card-bg, rgba(15,23,32,0.6)); box-shadow: var(--shadow-1, 0 6px 18px rgba(3,6,10,0.6)); padding: var(--space-3, 12px); }

  .table td { padding: 6px 0; border: none; display: flex; justify-content: space-between; align-items: center; }

  .symbolCell { gap: 10px; }
  .symbol { padding: 6px 8px; font-size: var(--fs-xs, 13px); }

  .secondaryDetailsRow { display: block; }

  /* Action button full width in card mode */
  .actions { justify-content: stretch; }
  .actions button { width: 100%; }

  /* Ensure the detail pane follows the list (stacked) */
  .leftPane, .rightPane { min-height: auto; }
}

.grid {
  display: grid;
  /* left column flexible but constrained, right column uses remaining space */
  /* clamp(min, preferred, max) keeps the left column readable while allowing the table to grow */
  grid-template-columns: clamp(280px, 22vw, 420px) 1fr;
  gap: 18px;
  /* create isolated stacking context so z-index between columns behaves predictably */
  isolation: isolate;
}

/* Ensure left column stays in the normal document flow by default so it does not overlay the table.
   The table will have a higher stacking context so table content isn't hidden by the left column. */
.left {
  display: grid;
  gap: 18px;
  min-width: 0;
  position: static; /* normal flow */
  z-index: auto;
  background: linear-gradient(180deg, rgba(7,12,18,0.92), rgba(7,12,18,0.88));
  padding: 14px;
  border-radius: 12px;
}

/* make sure direct children of .left (cards/forms) create their own stacking context when needed */
.left > * {
  position: static;
  z-index: auto;
  background-clip: padding-box; /* avoid shadows bleeding */
  min-width: 0; /* allow children to shrink properly in Firefox */
}

/* Remove the forced overlay in compact mode — compact will simply stack vertically without z-index tricks */
.grid.gridCompact .left {
  position: static;
  z-index: auto;
}

.grid.gridCompact .left > * {
  position: static;
  z-index: auto;
}

/* ensure right column can show overflow in compact mode (table will scroll under document flow) */
.grid.gridCompact .right {
  overflow: visible;
}

/* ensure right column content is clipped to its area so the table never visually overflows under the left column */
.right {
  display: grid;
  gap: 18px;
  min-width: 0; /* allow overflow within .tableWrap instead of pushing the whole grid */
  position: relative;
  z-index: 1;
  overflow: hidden; /* clip any visual overflow so left column is always unobstructed */
}

.form {
  display: grid;
  gap: 12px;
}

.row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
}

.actions {
  display: flex;
  gap: 12px;
  justify-content: flex-end;
  margin-top: 8px;
  flex-wrap: wrap; /* allow buttons to wrap on narrow screens */
}

.positionsPlaceholder {
  color: var(--muted);
  padding: 16px 0;
}

.headerRow {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  margin-bottom: 8px;
}

.title {
  font-size: 18px;
  font-weight: 700;
}

.controls {
  display: flex;
  align-items: center;
  gap: 8px;
}

.filterPills {
  display: flex;
  gap: 8px;
}

/* lower the table shadow and make the table not visually bleed into left column */
.table {
  position: relative;
  z-index: 20;
  width: 100%;
  min-width: 900px; /* ensure table columns don't collapse and cause overlaps */
  border-collapse: collapse;
  background: linear-gradient(180deg, rgba(255,255,255,0.01), rgba(255,255,255,0.005));
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(2,8,23,0.28);
}

/* Ensure tableWrap creates a proper clipping/scrolling container */
.tableWrap {
  overflow: auto;
  max-height: 72vh; /* allow more vertical space */
  width: 100%;
  -webkit-overflow-scrolling: touch;
  padding-bottom: 8px; /* breathing room for last row */
}

.table thead tr {
  border-bottom: 1px solid rgba(255,255,255,0.03);
}

.table th {
  text-align: left;
  padding: 12px 10px;
  color: var(--muted);
  font-size: 13px;
  position: sticky;
  top: 0;
  background: rgba(4,8,12,0.92);
  backdrop-filter: blur(6px);
  z-index: 25; /* stay above rows */
}

.table td {
  padding: 12px 10px;
  border-top: 1px solid rgba(255,255,255,0.02);
  vertical-align: middle;
}

/* make sure expanded rows and hover states don't visually overlap sticky header */
.mainRow:hover {
  background: linear-gradient(90deg, rgba(14,165,164,0.03), rgba(255,255,255,0.01));
}

.mainRowExpanded {
  background: linear-gradient(90deg, rgba(14,165,164,0.04), rgba(255,255,255,0.02));
}

.mainRowExpanded td {
  position: relative; /* keep expanded content within stacking context */
}

.chevCell {
  width: 40px;
  text-align: center;
  padding: 0 6px;
}

.chev {
  color: var(--muted);
  font-size: 14px;
  display: inline-block;
}

/* reduce table padding slightly when expanded for compact look */
.mainRowExpanded td {
  padding-top: 16px;
  padding-bottom: 16px;
}

.symbolCell {
  display: flex;
  align-items: center;
  gap: 8px;
}

.symbol {
  padding: 6px 10px;
  background: rgba(14,165,164,0.08);
  color: var(--text);
  border-radius: 8px;
  font-weight: 700;
  font-size: 13px;
}

.externalBtn {
  background: transparent;
  border: none;
  color: var(--muted);
  cursor: pointer;
  font-size: 14px;
}

.sideLong {
  color: var(--accent);
  font-weight: 700;
}

.sideShort {
  color: var(--danger);
  font-weight: 700;
}

/* small pill next to the side select to visually show LONG/SHORT color */
.sideBadge {
  display: inline-block;
  padding: 4px 8px;
  border-radius: 999px;
  background: rgba(255,255,255,0.02);
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  min-width: 44px;
  text-align: center;
}

/* stronger visual background for badge variants */
.sideBadge.sideLong {
  background: rgba(105,240,174,0.10);
  color: #69f0ae;
}
.sideBadge.sideShort {
  background: rgba(255,107,107,0.10);
  color: #ff6b6b;
}

.plPositive {
  color: #7ef18a;
  font-weight: 700;
}

.plNegative {
  color: #ff6b6b;
  font-weight: 700;
}

.secondaryRow td {
  padding: 8px 10px 18px 10px;
  background: linear-gradient(180deg, rgba(255,255,255,0.01), rgba(255,255,255,0.003));
}

.secondaryInner {
  display: flex;
  gap: 18px;
  align-items: flex-start;
  justify-content: space-between;
}

.secondaryLeft {
  color: var(--muted);
  font-size: 13px;
  min-width: 220px;
}

.secondaryRight {
  color: var(--muted);
  display: grid;
  grid-template-columns: repeat(2, auto);
  gap: 8px 22px;
  font-size: 13px;
}

.marketHeader {
  margin: 8px 0 12px 0;
  padding: 10px 12px;
  border-radius: 8px;
  background: linear-gradient(90deg, rgba(14,165,164,0.02), rgba(255,255,255,0.003));
  color: var(--muted);
  font-weight: 600;
}

.statusOpen {
  display: inline-block;
  padding: 6px 10px;
  border-radius: 999px;
  background: rgba(110,231,183,0.08);
  color: #69f0ae;
  font-weight: 700;
  font-size: 12px;
}

.statusClosed {
  display: inline-block;
  padding: 6px 10px;
  border-radius: 999px;
  background: rgba(255,255,255,0.02);
  color: var(--muted);
  font-weight: 700;
  font-size: 12px;
}

/* Filled status (blue) */
.statusFilled {
  display: inline-block;
  padding: 6px 10px;
  border-radius: 999px;
  background: rgba(203,213,225,0.06);
  color: #60a5fa;
  font-weight: 700;
  font-size: 12px;
}

.filterPill {
  padding: 6px 10px;
  border-radius: 10px;
}

.secondaryDetailsRow {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 12px 18px;
  margin-bottom: 10px;
  font-size: 13px;
  color: var(--muted);
}

.secondaryNotesRow {
  grid-column: 1 / -1;
  background: rgba(14,165,164,0.04);
  border-radius: 6px;
  padding: 10px 12px 8px 12px;
  font-size: 13px;
  color: var(--muted);
  margin-top: 2px;
}

.positionsList {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.positionItem {
  display: flex;
  align-items: center;
  gap: 14px;
  background: rgba(14,165,164,0.06);
  border-radius: 6px;
  padding: 7px 10px;
  font-size: 13px;
  color: var(--muted);
}

.positionExpandRow {
  margin-top: 10px;
  padding: 10px 12px 8px 32px;
  background: rgba(14,165,164,0.04);
  border-radius: 6px;
}

/* Normalize bold labels inside expanded rows to small/muted (Entry/SL/TPs) */
.positionExpandRow strong,
.secondaryDetailsRow strong,
.positionExpandRow .small,
.positionExpandRow .metaPiece,
.secondaryDetailsRow .small {
  font-weight: 400; /* normal */
  color: var(--muted);
}

/* Make the first-line inside positionExpandRow render like the small inline meta (Entry/SL/TPs)
   without requiring JSX changes: flex layout, small font and muted colour, no bold. */
.positionExpandRow > div {
  display: flex;
  gap: 12px;
  align-items: center;
  flex-wrap: wrap;
  font-size: 12px;
  color: var(--muted);
}
.positionExpandRow > div > div,
.positionExpandRow > div > span {
  font-size: 12px;
  color: var(--muted);
  font-weight: 400;
}
.positionExpandRow strong {
  font-weight: 400;
  color: var(--muted);
  margin-right: 4px;
}

.positionExpandGrid {
  display: grid;
  grid-template-columns: repeat(4, 1fr) 120px;
  gap: 10px 18px;
  align-items: end;
}

.input, .tpInput {
  background: transparent;
  border: 1.5px solid transparent;
  border-radius: 4px; /* immer gleich, kein Sprung */
  color: var(--color-text, #e6e6e6);
  font-size: 1rem;
  font-family: inherit;
  padding: 0;
  outline: none;
  min-width: 60px;
  max-width: 100px;
  width: 100%;
  box-sizing: border-box;
  height: 24px;
  line-height: 24px;
  vertical-align: middle;
  transition: background 0.2s, box-shadow 0.2s, border-color 0.2s;
  text-align: left;
  appearance: none;
  -webkit-appearance: none;
}
.input:focus, .tpInput:focus {
  background: rgba(255,255,255,0.04);
  border-radius: 4px;
  border-color: var(--color-primary, #00bcd4);
  outline: none;
  box-shadow: none;
}
.table th, .table td {
  min-width: 60px;
  box-sizing: border-box;
  height: 24px;
  line-height: 24px;
  vertical-align: middle;
}
.table th.symbolCell, .table td.symbolCell {
  min-width: 90px;
  width: 90px;
}
.table th, .table td {
  /* für Entry, SL, TP, Margin, Leverage feste Breite */
}
.table th:nth-child(5), .table td:nth-child(5), /* Entry */
.table th:nth-child(6), .table td:nth-child(6), /* SL */
.table th:nth-child(7), .table td:nth-child(7), /* Margin */
.table th:nth-child(8), .table td:nth-child(8), /* Leverage */
.table th:nth-child(9), .table td:nth-child(9)  /* P&L */
{
  min-width: 80px;
  width: 80px;
}

/* responsive tweaks */
@media (max-width: 900px) {
  .grid {
    grid-template-columns: 1fr;
  }

  .row {
    grid-template-columns: 1fr;
  }

  .table {
    min-width: 680px; /* allow horizontal scroll but keep columns readable */
  }

  .secondaryInner {
    flex-direction: column;
    gap: 8px;
  }
}

/* Ultra-wide tweaks: make table header slightly larger and increase gaps for readability */
@media (min-width: 1600px) {
  .table th {
    font-size: 14px;
    padding: 14px 12px;
  }

  .table td {
    padding: 14px 12px;
  }

  .secondaryRight {
    gap: 10px 32px;
  }
}

.gridCompact {
  grid-template-columns: 1fr !important;
}

/* When the grid is compact, stack left pane content and hide the right pane for mobile */
.grid.gridCompact .rightPane {
  display: none;
}

.grid.gridCompact .leftPane {
  width: 100%;
  min-width: 0;
}

/* ensure listAndDetailWrap behaves when compact: left pane should be full width */
.grid.gridCompact .listAndDetailWrap {
  display: block;
}

