/* Ensure editor inside rightPane stretches nicely */
.rightPane .editor {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

/* make the right column card content scrollable when content grows */
.rightPane {
  display: flex;
  flex-direction: column;
  gap: 12px;
  overflow: hidden;
}

.rightPane .tableWrap, .rightPane > * {
  overflow: auto;
}

/* keep a nice margin between list and detail on small screens */
@media (max-width: 980px) {
  .listAndDetailWrap { gap: 10px }
  .leftPane, .rightPane { padding: 10px }
}

/* Fullscreen variant for the main grid: use available viewport height and allow left/right panes to scroll independently */
.fullScreen {
  min-height: calc(100vh - 80px); /* account for app header bar height — adjust if needed */
  height: calc(100vh - 80px);
}

.fullScreen .left, .fullScreen .right {
  min-height: 0; /* allow flex children to size correctly inside the grid */
}

/* make left and right columns scroll independently */
.fullScreen .left {
  overflow: auto;
  max-height: 100%;
}

.fullScreen .right {
  overflow: auto;
  max-height: 100%;
}

/* ensure inner cards stretch to fill column height when needed */
.fullScreen .left > * , .fullScreen .right > * {
  display: flex;
  flex-direction: column;
  height: 100%;
}

/* ensure tableWrap inside right column scrolls properly when fullScreen */
.fullScreen .right .tableWrap {
  max-height: calc(100vh - 220px);
}

/* Trades header with inline filters */
.tradesHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  margin: 8px 0 12px 0;
}

.tradesTitle {
  font-size: 14px;
  font-weight: 700;
  color: var(--text);
}

.tradesControls {
  display: flex;
  align-items: center;
  gap: 12px;
}

/* New: small row under the Trades title that contains the status filter pills (All/Open/Closed/Filled) */
.tradesStatusRow {
  display: flex;
  align-items: center;
  gap: 10px;
}

/* Ensure status pills inside the row render as small buttons by reusing filter styling and spacing */
.tradesStatusRow .button {
  /* leave visual styling to Button.module.css; ensure compact spacing */
  padding: 6px 10px;
  min-height: 34px;
}

.tradesFilters {
  display: flex;
  align-items: center;
  gap: 10px;
}

.tradesCount {
  margin-left: auto;
  color: var(--muted);
  font-size: 13px;
}

/* Responsive: stack header elements on small screens */
@media (max-width: 680px) {
  .tradesHeader {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }
  .tradesControls { width: 100%; display: flex; justify-content: space-between; }
}

/* New Trade form layout (left column) */
.newTradeWrapper {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.newTradeHeader {
  padding: 6px 8px;
}

/* two-column grid for form fields; collapses to single column on narrow widths */
.newTradeGrid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
  align-items: start;
}

.newTradeField {
  display: flex;
  flex-direction: column;
  gap: 8px;
  min-width: 0; /* allow children to shrink in Firefox */
}

/* full-width field (notes, etc.) */
.full {
  grid-column: 1 / -1;
}

/* make inputs fill their container and respect box model */
.input, .tpInput, input, select, textarea {
  width: 100%;
  box-sizing: border-box;
}

/* Align actions: Reset left, Add right */
.newTradeWrapper .actions {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
}

@media (max-width: 720px) {
  .newTradeGrid { grid-template-columns: 1fr; }
  .newTradeWrapper .actions { justify-content: flex-end; }
}

/* Mock data loader modal styles */
.backdrop {
  position: fixed;
  inset: 0;
  background: rgba(2,6,10,0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
}

.mockDialog {
  background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));
  border-radius: 10px;
  padding: 18px;
  width: 520px;
  max-width: calc(100% - 40px);
  box-shadow: 0 12px 40px rgba(0,0,0,0.6);
  color: var(--text);
}

.mockDialog h3 {
  margin: 0 0 8px 0;
  font-size: 16px;
}

.mockDialog p {
  margin: 0 0 12px 0;
  color: var(--muted);
}

.mockOption {
  padding: 8px 12px;
  border-radius: 8px;
  border: 1px solid rgba(255,255,255,0.03);
  background: rgba(255,255,255,0.01);
  color: var(--text);
  cursor: pointer;
}

.mockOption[aria-pressed="true"] {
  background: linear-gradient(90deg, rgba(124,92,255,0.14), rgba(34,193,195,0.06));
  color: var(--text);
  box-shadow: 0 6px 20px rgba(34,193,195,0.06);
  transform: translateY(-1px);
}

/* Compact-mode helpers for stacked list+detail */
.compactSummary {
  background: linear-gradient(180deg, rgba(255,255,255,0.01), rgba(255,255,255,0.005));
  border-radius: 8px;
  padding: 10px;
  box-shadow: 0 6px 18px rgba(2,8,23,0.35);
}

.compactControls {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 8px;
}

.compactPlaceholder {
  padding: 12px;
  border-radius: 8px;
  background: linear-gradient(180deg, rgba(255,255,255,0.01), rgba(255,255,255,0.005));
  color: var(--muted);
}

/* subtle transition for editor area when toggling */
.leftPane > * {
  transition: max-height 220ms ease, opacity 180ms ease;
}
