/* Global styles and CSS variables for the app */
:root {
  --bg: #0b1220; /* dark navy */
  --surface: #07121a;
  --text: #e6eef8;
  --muted: #9fb0c6;
  --color-text: #e6e6e6;
  --color-primary: #00bcd4;
  color-scheme: dark; /* hint to Chromium to use dark native UI for controls like datepickers */
}

html, body, #root {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  background: radial-gradient(1200px 600px at 10% 10%, rgba(88,214,141,0.06), transparent 10%), linear-gradient(180deg, var(--bg), var(--surface));
  color: var(--text);
  font-family: Inter, ui-sans-serif, system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Make default link color readable in dark mode */
a {
  color: var(--text);
}

/* Utility container to give a max-width and padding */
.app-container {
  /* Fluid/clamped width: grows on big screens but stays readable */
  max-width: clamp(1200px, 92vw, 1800px);
  margin: 0 auto;
  padding: 16px 24px;
}

/* Hide default browser spinner/arrows for input[type=number] */
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield; /* Firefox */
}

