.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  padding: 14px 18px;
  background: linear-gradient(90deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));
  border-radius: 10px;
  position: relative;
}

.brand {
  font-weight: 900;
  letter-spacing: 0.3px;
  font-size: 20px;
  color: var(--text);
}

.nav {
  display: flex;
  gap: 12px;
}

.nav a {
  color: var(--muted);
  text-decoration: none;
  font-weight: 600;
}

.nav a:hover {
  color: var(--text);
}

.controls {
  display: flex;
  align-items: center;
  gap: 8px;
}

.hamburger {
  display: none;
  background: transparent;
  border: none;
  color: var(--text);
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

@media (max-width: 720px) {
  .nav { display: none; }
  .hamburger { display: inline-flex; align-items: center; justify-content: center; }
}

/* When mobile nav is opened (JS toggles .open) show as vertical list */
.mobileNav {
  display: none;
}
.mobileOpen {
  display: block;
  position: absolute;
  top: 56px;
  right: 12px;
  /* Use a solid background to avoid transparency issues on dark headers */
  background: var(--card-bg-solid, rgba(10,12,15,1));
  border-radius: 8px;
  padding: 8px;
  box-shadow: var(--shadow-1, 0 10px 30px rgba(0,0,0,0.6));
  z-index: 3000;
}
.mobileNav a { display: block; padding: 8px 12px; color: var(--text); text-decoration: none; }

/* ensure focus styles inside mobile nav */
.mobileNav a:focus {
  outline: none;
  box-shadow: 0 0 0 4px rgba(88,214,141,0.12);
}

/* ensure hamburger button is always above nav for click handling */
.hamburger { z-index: 3100; }

/* backdrop behind mobile nav */
.backdrop {
  display: none;
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.45);
  z-index: 2500;
  opacity: 0;
  transition: opacity 220ms ease;
}
.backdropVisible {
  display: block;
  opacity: 1;
}

/* slide-in mobile nav */
.mobileNav {
  display: none;
  transform: translateY(-8px) translateX(8px) scale(0.98);
  opacity: 0;
  transition: transform 220ms ease, opacity 180ms ease;
}
.mobileOpen {
  display: block;
  transform: translateY(0) translateX(0) scale(1);
  opacity: 1;
}
